import { Injectable ,Injector} from '@angular/core';
import { HttpInterceptor } from '@angular/common/http';
import { LoginserviceService } from './service/login/loginservice.service';

@Injectable({
  providedIn: 'root'
})
export class TokenInterceptorService implements HttpInterceptor {

  intercept(req, next){
    let token=localStorage.getItem("token");

    if(token)
    {
     // console.log("inter ceptor"+token)
      let token2="token ";
    //  console.log(token2+token)
      token2=token2+token;
      const authReq=req.clone({
        setHeaders : { Authorization:token2 }
      })
      return next.handle(authReq);
    }
    else
    {
     // console.log("no token")
      //return next.handle(req);
      return next.handle(req);
    }
  }

  constructor(private injector:Injector,private logisService:LoginserviceService) { }
}
