import { Component, OnInit, Inject } from '@angular/core';
import { Router } from '@angular/router';
import { LoginserviceService } from '../service/login/loginservice.service';
import { NgForm } from '@angular/forms';
import {ConfirmationService, Message} from 'primeng/components/common/api';
import {MessageService} from 'primeng/components/common/messageservice';
import {DialogModule} from 'primeng/dialog';
@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css'],
  providers: [ConfirmationService,MessageService]
})

export class LoginComponent implements OnInit {
userName:string;
password:string;
invalidLogin:boolean;
display: boolean = false;

showDialog() {
    this.display = true;
}
public error:boolean=false
public error1:boolean=false
  constructor(private router:Router,private loginService:LoginserviceService ) {

   }

  ngOnInit() {
    localStorage.setItem("url",localStorage.getItem("url"))

  }

  successfulResponse(response)
  {
    console.log(response)
    this.error=false
    console.log("succ");
   localStorage.setItem("token",response.token)
   this.router.navigate(['device']);
  }
errorResponse(error)
  {
  //  console.log("testerror");
    console.log(error.status);
   // console.log(error.statusText)
   if(error.status==400)
   this.error=true
   if(error.status==0)
   this.error1=true
   if(error.status==404)
   this.error1=true

}

isUserLoggedIn()
  {
    let user=sessionStorage.getItem('authenticateuser')
    return !(user===null)
  }

  login(form:NgForm)
  {
    this.error=false
    this.error1=false
    let obj:UserLoginModel=new UserLoginModel(this.userName,this.password)
    console.log("login")
    console.log(obj)
   this.loginService.loginUser(obj).subscribe(res=>this.successfulResponse(res),error=>this.errorResponse(error))

  }
  public ip:string=null;
  public port:string=null;
  basicSettings()
  {
    localStorage.removeItem("url")
    let url='http://'
    url=url+this.ip
   // url=url+':'
   // url=url+this.port
    localStorage.setItem("url",url)
    console.log(url)
    this.display=false;

  }
}

export class UserLoginModel
{
  constructor(public username:string,public password:string)
  {

  }
}
