export class TodoItemNode {
  children: TodoItemNode[];
  item: string;

}

/** Flat to-do item node with expandable and level information */
export class TodoItemFlatNode {
  item: string;
  level: number;
  expandable: boolean;
}

/**
 * The Json object for to-do list data.
 */
var TREE_DATA = {
};

/** 
 * Checklist database, it can build a tree structured Json object.
 * Each node in Json object represents a to-do item or a category.
 * If a node is a category, it has children items and new items can be added under the category.
 */
@Injectable()
export class ChecklistDatabase {
  favoriteSeason: string;
  dataChange = new BehaviorSubject<TodoItemNode[]>([]);

  get data(): TodoItemNode[] 
  {

     return this.dataChange.value; 
   
    }

  constructor(private service:DeviceserviceService,private PersonnelService:PersonnelService) {

 // this.getallParentDepartmentServer();
 /* console.log("before Initializing")
  console.log(TREE_DATA)
  console.log("After Initializing")
   console.log("First Constructor Finished"+TREE_DATA)*/
  }
/*  getallParentDepartmentServer(){
    console.log("getallParentDepartmentServer")
    this.PersonnelService.getAllParentDepartmentDetailsBegining().subscribe(response=>this.succsess(response),error=>this.errorHandlin(error))
  }*/
  /*succsess(response)
  {
    console.log("Succes");
    console.log("TREE DATA-------------");
    console.log(TREE_DATA);
    console.log("response--------------");
    console.log(response);
    TREE_DATA=response;
    console.log("responses----------------Changed to TREE_DATA");
    console.log(TREE_DATA);
    this.initialize();
  }*/
 /* errorHandlin(error)
    {
      console.log("Eroor");
    }*/


  initialize() {
    let data = this.buildFileTree(TREE_DATA, 0);
    this.dataChange.next(data);
  }

  /**
   * Build the file structure tree. The `value` is the Json object, or a sub-tree of a Json object.
   * The return value is the list of `TodoItemNode`.
   */
  buildFileTree(obj: {[key: string]: any}, level: number): TodoItemNode[] {
   // console.log("in buildFileTree ")
    return Object.keys(obj).reduce<TodoItemNode[]>((accumulator, key) => {
      const value = obj[key];
      const node = new TodoItemNode();
      node.item = key;

      if (value != null) {
        if (typeof value === 'object') {
          node.children = this.buildFileTree(value, level + 1);
        } else {
          node.item = value;
        }
      }

      return accumulator.concat(node);
    }, []);
  }

  /** Add an item to to-do list */
  insertItem(parent: TodoItemNode, name: string) {
    if (parent.children) {
      parent.children.push({item: name} as TodoItemNode);
      this.dataChange.next(this.data);
    }
  }

  updateItem(node: TodoItemNode, name: string) {
    node.item = name;
    this.dataChange.next(this.data);
  }

}

