import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { OracleDdModel } from './oracleDbModel';
import { SystemService } from 'src/app/service/system.service';
import {ConfirmationService, Message} from 'primeng/components/common/api';
import {MessageService} from 'primeng/components/common/messageservice';
@Component({
  selector: 'app-oracle-data-setup',
  templateUrl: './oracle-data-setup.component.html',
  styleUrls: ['./oracle-data-setup.component.css'],
  providers: [ConfirmationService,MessageService]
})
export class OracleDataSetupComponent implements OnInit {
  public db_name:string=null
  public db_password:string=null
  public ip_add:string=null
  public port:string=null
  public db_serves:string=null
  public table_name:string=null
  public upload_mode:string=null
  public time_interval:string=null;
  msgs: Message[] = [];
  public uploadMode_array = [
    {id: 0, name: "Auto"},
    {id: 1, name: "Time"},
    {id: 2, name: "Manual"}
  ];
  public uploadMode_init:number=0;
  public uploadMode_data:string=this.uploadMode_array[this.uploadMode_init].name;
  public isEnabled=true;
  constructor(private systemservices:SystemService) { }

  ngOnInit() {
    this.systemservices.getoracleDBSettings().subscribe(
      response=>this.SuccesssOracleDBGet(response),
      error=>this.ErrorOracleDBGet(error))

  }

  getDbSettings(form:NgForm)
  {
    console.log(form)
    let obj:OracleDdModel=new OracleDdModel(this.db_name,this.db_password,this.ip_add,this.port,this.db_serves,this.table_name, this.uploadMode_data,this.time_interval);
    console.log(obj)
    this.systemservices.oracleDBSettingPost(obj).subscribe(
      response=>this.SuccesssOracleDB(response),
      error=>this.ErrorOracleDB(error))

  }
  uploadmodeMethod(data:any)
  {
    console.log(data)
    if(data==1)
     this.isEnabled=false;
     else
     this.isEnabled=true;
    this.uploadMode_data=this.uploadMode_array[data].name

  }
  SuccesssOracleDB(response)
  {
    console.log(response)
    console.log(response.responce_data)
    if(response.responce_data="Success")
    {
      this.showError1("New DB Setting Successfully Added");
      this.msgs.push({severity:'success', summary:'Success Message', detail:'Record is Saved'});
    }
    else
    {
      this.showError1(response.responce_data);
    }
  }
  ErrorOracleDB(error)
  {
    console.log(error)
    console.log(error.responce_data)
    this.showError();
  }
  SuccesssOracleDBGet(response:OracleDdModel)
  {
    console.log("edit mode suucess")
    console.log(response)
    this.db_name=response[0].db_name;
    this.db_password=response[0].db_password;
    this.ip_add=response[0].ip_add;
    this.port=response[0].port;
    this.db_serves=response[0].db_serves;
    this.table_name=response[0].table_name;
    this.uploadMode_data=response[0].upload_mode;
    if(this.uploadMode_data=='Auto')
    this.uploadMode_init=0
    if(this.uploadMode_data=='Time')
    this.uploadMode_init=1
    if(this.uploadMode_data=='Manual')
    this.uploadMode_init=2
    this.time_interval=response[0].time_interval;

  }
  ErrorOracleDBGet(error)
  {
    console.log(error)
    console.log(error.responce_data)
    this.showError()
  }
  showError1(data:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message', detail:data});
  }
  showError() {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message', detail:"Server is Not Responding,Please Try Again later"});
  }
}
