import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { suuceesDaa } from './personnel/personnel.service';
import { OracleDdModel } from '../system/oracle-data-setup/oracleDbModel';
@Injectable({
  providedIn: 'root'
})
export class SystemService {
ip_and_port:string

constructor(private http:HttpClient) {
  this.ip_and_port=localStorage.getItem("url")
  //this.ip_and_port=environment.apiUrl
}

oracleDBSettingPost(JsonData)
{
  console.log("System Service : oracleDBSettingPost");
    return this.http.post<suuceesDaa>(this.ip_and_port+'/angreq/accounts/orcl_db',JsonData);
}
getoracleDBSettings()
{
  console.log("System Service : oracleDBSettingPost");
    return this.http.post<OracleDdModel>(this.ip_and_port+'/angreq/accounts/orcl_db_get',null);
}
newUserRegistration(JsonData)
{
  console.log("System Service : newUserRegistration");
    return this.http.post<suuceesDaa>(this.ip_and_port+'/angreq/accounts/register',JsonData);
}
}
