import { Component, OnInit } from '@angular/core';
import { CarServiceService } from 'src/app/car-service.service';
import { ConfigurationService } from 'src/app/service/InitialConfig/configuration.service';

import {Message} from 'primeng/components/common/api';
import {MessageService} from 'primeng/components/common/messageservice';
@Component({
  selector: 'app-shopping-edit',
  templateUrl: './shopping-edit.component.html',
  styleUrls: ['./shopping-edit.component.css'],
  providers: [MessageService]
})

export class ShoppingEditComponent implements OnInit {
  msgs: Message[] = [];
  cars:any;
  first: number = 0;
  cols: any[];

  constructor(private carService: CarServiceService,public config:ConfigurationService) { }

  ngOnInit() {
    this.carService.getCarsSmall().subscribe(cars => this.cars = cars);
 

      this.cols = [
          { field: 'vin', header: 'Vin' },
          {field: 'year', header: 'Year' },
          { field: 'brand', header: 'Brand' },
          { field: 'color', header: 'Color' }
      ];
      this.showSuccess()
  }
  showSuccess() {
    this.msgs = [];
    this.msgs.push({severity:'success', summary:'Success Message', detail:'Order submitted'});
    
}
showError() {
  this.msgs = [];
  this.msgs.push({severity:'error', summary:'Error Message', detail:'Validation failed'});
}

clear() {
  this.msgs = [];
}
 
}


