import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { ResponseModel } from '../attendance/attendance-service.service';
//var electron=require('electron')

@Injectable({
  providedIn: 'root'
})



export class LoginserviceService {
  ip_and_port:string
  constructor(public http:HttpClient) {
    this.ip_and_port=localStorage.getItem("url")
   // console.log("ip and Port")
    //console.log(this.ip_and_port)
  }
  loginUser(JsonData)
  {
    this.ip_and_port=localStorage.getItem("url")
    console.log("ip and Port ")
    console.log(this.ip_and_port)

   // let rootVar = window["url"];
  //  console.log("break point 1"+rootVar);
    return this.http.post<ResponseModel>(this.ip_and_port+'/angreq/accounts/login',JsonData);
  }
  loggedIn()
  {
  //  console.log("is logged in")
  //  console.log(!!localStorage.getItem("token"))
    return !!localStorage.getItem("token")
  }
  getToken()
  {
    return localStorage.getItem("token");
  }


}
