import { Component, OnInit, OnDestroy } from '@angular/core';
import { DeviceserviceService, RealTime, RealTimeComment } from 'src/app/service/device/deviceservice.service';
import { interval } from 'rxjs';
import { MessageService } from 'primeng/components/common/messageservice';
import { Message } from 'primeng/components/common/api';


@Component({
  selector: 'app-devicerealtimesubmenu',
  templateUrl: './devicerealtimesubmenu.component.html',
  styleUrls: ['./devicerealtimesubmenu.component.css'],
  providers: [MessageService]
})
export class DevicerealtimesubmenuComponent implements OnInit, OnDestroy  {
  msgs: Message[] = [];
  cols;
  public datas:any[];
  datas1;
  errordisplayflag:boolean=false;
  errorDta="this notan eroor frommy side"
  constructor(private service:DeviceserviceService) {
    let obj:RealTimeComment=new RealTimeComment("start")
    this.service.realTime(obj).subscribe(
      response=>this.handleSuccess(response),
      error=>this.handleError(error)
     );

    console.log("constructor worked")
  }
  ngOnDestroy() {
   // alert(`I'm leaving the app!`);
    let obj:RealTimeComment=new RealTimeComment("end")
    this.service.realTime(obj).subscribe(
      response=>this.handleSuccess(response),
      error=>this.handleError(error)
     );
     if (this.interval) {
      clearInterval(this.interval);
      console.log("ngOnDestroy")
   }
   }
public interval;

  ngOnInit() {

    this.cols = [
      { field: 'device_name', header: 'Device Name'},
      { field: 'device_SN', header: 'Serial Number' },
      { field: 'punch_status', header: 'Punch_status' },
      { field: 'punch_time', header: 'Area punch_time' },
      { field: 'last_id', header: 'last_id' },
      { field: 'personal_No', header: 'Last personal_No' },
      { field: 'area_name', header: 'area_name'},
      { field: 'verification_mode', header: 'verification_mode'},
      { field: 'work_code', header: 'work_code'}

  ];

  // interval(1000).subscribe(x=>console.log(x))
  let obj:RealTimeComment=new RealTimeComment("start")
   this.interval=interval(10000).subscribe(()=>this.service.realTime(obj).subscribe(
      response=>this.handleSuccess(response),
      error=>this.handleError(error)
     ));
     console.log("ng on init worked")
}
handleSuccess(response)
{
console.log("handleSuccess real time");

console.log(response)
if(response=="No data")
{
this.showError1("Please Waite Some More Time,")
this.datas=response;
}
else
this.datas=response;

}
handleError(error)
{
  console.log("handle  Error");
  console.log(error);
  this.datas=error;
  this.showError()
}

showError1(data:string) {
  this.msgs = [];
  this.msgs.push({severity:'info', summary:'Info Message : ', detail:data});
}
showError() {
  this.msgs = [];
  this.msgs.push({severity:'error', summary:'Error Message : ', detail:"Server is Not responding,Please Try again Later"});
}


}

