import { Component, OnInit } from '@angular/core';
import {ConfirmationService, Message} from 'primeng/components/common/api';
import {MessageService} from 'primeng/components/common/messageservice';
import { SystemService } from 'src/app/service/system.service';
import { NgForm } from '@angular/forms';
import { newNormalUserModel } from './NewNormalUserModel';
@Component({
  selector: 'app-add-new-normal-user',
  templateUrl: './add-new-normal-user.component.html',
  styleUrls: ['./add-new-normal-user.component.css'],
  providers: [ConfirmationService,MessageService]
})
export class AddNewNormalUserComponent implements OnInit {
  msgs: Message[] = [];
     public username:string
    public password:string
    public email:string
    public first_name:string
    public last_name:string
    public is_superuser:boolean=true;
    public is_staff:boolean=false
    public is_active:boolean=false

    public userType_array = [
      {id: 0, name: "Super Admin"},
      {id: 1, name: "Staff"},
    ];
    public userType_init:number=0;
    public userType_data:string=this.userType_array[this.userType_init].name;

    public userStatus_array = [
      {id: 0, name: "Inactive"},
      {id: 1, name: "Active"},
    ];
    public userStatus_init:number=0;
    public userStatus_data:string=this.userStatus_array[this.userStatus_init].name;
  constructor(private systemservices:SystemService) { }

  ngOnInit() {
  }
  getaddNewUserForm(form:NgForm)
  {
    let obj:newNormalUserModel=new newNormalUserModel(this.username,this.password,this.email,this.first_name,
    this.last_name,
    this.is_superuser,
    this.is_staff,
    this.is_active)
    console.log(obj)
    this.systemservices.newUserRegistration(obj).subscribe(
        response=>this.Successs(response),
        error=>this.Error(error))
  }
  userTypeMethod(data:any)
  {
    console.log(data)
    this.userType_init=data;
    if(this.userType_init==0)
    {
    this.is_superuser=true;
    this.is_staff=false;
    }
    if(this.userType_init==1)
    {
    this.is_staff=true;
    this.is_superuser=false;
    }

  }
  userStatusMethod(data:any)
  {
    console.log(data)
    if(data==0)
    this.is_active=false;
    if(data==1)
    this.is_active=true;
  }
  Successs(response)
  {
    console.log(response)
    console.log(response.responce_data)
    if(response.responce_data=="Success")
    {
      this.showError1("New User Successfully Added");
    }
    else
    {
      this.showError1(response.responce_data);
    }
  }
  Error(error)
  {
    console.log(error)
    console.log(error.responce_data)
    this.showError()
  }
  showError1(data:string) {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message', detail:data});
  }
  showError() {
    this.msgs = [];
    this.msgs.push({severity:'info', summary:'Message', detail:"Server is Not Responding,Please Try Again later"});
  }
}
