import { Component, OnInit } from '@angular/core';
import { PersonnelService } from 'src/app/service/personnel/personnel.service';
import { AddpersonnelModel } from 'src/app/personnel/personnels/personnels.component';
import { DatePipe } from '@angular/common';
import { AttendanceServiceService } from 'src/app/service/attendance/attendance-service.service';
import { getShiftModel } from '../Model/TimeTableModels';
import { addSheduleModel } from '../Model/AddSheduleModel';

@Component({
  selector: 'app-add-shedule',
  templateUrl: './add-shedule.component.html',
  styleUrls: ['./add-shedule.component.css'],
  providers:[DatePipe]
})
export class AddSheduleComponent implements OnInit {
cols:any
personnel:AddpersonnelModel[];
public myDate:String="2020-01-01";
public endDate="2020-01-01";
public myDate1:String="2020-01-01";
public stratDate="2020-01-01";
public Removeoverlap:boolean=false;
public ShiftNames=new Array();
public SelectedShiftName:string;
public employeeSL_no:string=null;
  constructor(private PersonnelService1:PersonnelService,public datepipe: DatePipe,private attendanceService:AttendanceServiceService ) { }

  ngOnInit() {
    this.initialmethod()
    this.initialmethod2()
    this.cols = [
      { field: 'personnel_No', header: 'Personnel No',width: '10%' },
      { field: 'first_name', header: 'First Name',width: '20%' },
     
      { field: 'department', header: 'Department',width:'25%' },
      
      { field: 'Action', header: '',width: '25%' }
  ];
  }
  initialmethod(){
    this.PersonnelService1.getAlPersonnelDetailsBegining().subscribe(
      response=>this.handleSuccess1(response),
      error=>this.handleError1(error)
    );
  }
  initialmethod2(){
    this.attendanceService.Get_Shift_List().subscribe(
      res=>this.handleSuccess2(res),
      error1=>this.handleError2(error1)
    );
  }
  handleSuccess2(response1:getShiftModel[])
  {
  console.log("handleSuccess Shift List");
  console.log("SGIHT NAMES")
  for (let entry of response1) {
   console.log(entry.shift_name)
   this.ShiftNames.push(entry.shift_name)
  }
  this.SelectedShiftName=this.ShiftNames[0];
  
  }
  handleError2(error)
  {
    console.log("handle  Error");
    console.log(error);
  }


handleSuccess1(response)
{
console.log("handleSuccess");
console.log(response)
this.personnel=response;
}
handleError1(error)
{
  console.log("handle  Error");
  console.log(error);
}
birthDate_Method(){
  this.stratDate =this.datepipe.transform(this.myDate1, 'yyyy-MM-dd');
  console.log("Birth Date :"+ this.stratDate) 
  }
  
employmentDate_Method(){
  
  this.endDate =this.datepipe.transform(this.myDate, 'yyyy-MM-dd');
  console.log("Employment Date :"+ this.endDate) 
  }
  onRowEditSave(data:any) 
    {
      console.log(data['personnel_No'])
      this.employeeSL_no=data['personnel_No'];
    }
    Shift_Selection_Method(data:any)
    {
      console.log(data.target.value)
      this.SelectedShiftName=data.target.value;
    }
    formDataSubmit()
    {
      let obj:addSheduleModel=new addSheduleModel(this.stratDate,this.endDate,this.SelectedShiftName, this.employeeSL_no,this.Removeoverlap);
      this.attendanceService.Get_Shift_List().subscribe(
        res=>this.handleSuccess3(res),
        error1=>this.handleError3(error1)
      );
    }
    handleSuccess3(response1:getShiftModel[])
    {
    console.log("handleSuccess Shift List");
    console.log("SGIHT NAMES")
    for (let entry of response1) {
     console.log(entry.shift_name)
     this.ShiftNames.push(entry.shift_name)
    }
    this.SelectedShiftName=this.ShiftNames[0];
    
    }
    handleError3(error)
    {
      console.log("handle  Error");
      console.log(error);
    }
  
}
